@charset "UTF-8";
.game {
  display: flex;
  flex-wrap: wrap;
  margin: 30px auto 0;
  width: 710px;
  padding: 20px;
  background-color: #1e2b40;
  border-radius: 6px; }
  .game__top {
    background-color: #071224;
    border-radius: 6px;
    width: 100%;
    padding: 23px 15px; }
  .game__number-wrap {
    width: 100%;
    display: flex;
    justify-content: center; }
  .game__prediction {
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 184px;
    height: 140px;
    background-color: #1e2b40;
    border-radius: 6px;
    color: #fff; }
    .game__prediction-value {
      margin-bottom: 10px;
      font-size: 80px;
      font-family: PingFangSC-Semibold;
      line-height: 1; }
    .game__prediction-label {
      font-size: 14px;
      color: #c9c9c9; }
  .game__result {
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 184px;
    height: 140px;
    background-color: #1e2b40;
    border-radius: 6px; }
    .game__result-value {
      margin-bottom: 10px;
      font-size: 80px;
      font-family: PingFangSC-Semibold;
      line-height: 1;
      color: #40d690; }
    .game__result-label {
      font-size: 14px;
      color: #c9c9c9; }
  .game__slider-wrap {
    display: flex;
    margin: 20px 0 0;
    width: 100%; }
    .game__slider-wrap-center {
      padding: 0 20px;
      flex: 1; }
    .game__slider-wrap > button {
      width: 110px; }
  .game__info {
    background-color: #071224;
    border-radius: 6px;
    margin-top: 20px;
    width: 100%;
    height: 102px;
    display: flex;
    align-items: stretch; }
    .game__info-item {
      position: relative;
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      line-height: 1; }
      .game__info-item:after {
        position: absolute;
        content: '';
        top: 25px;
        bottom: 25px;
        right: 0;
        width: 1px;
        background-color: #333; }
      .game__info-item:last-child:after {
        display: none; }
      .game__info-item > div:first-child {
        margin-bottom: 18px;
        font-size: 14px;
        color: #7b7b7b; }
      .game__info-item > div:last-child {
        font-size: 20px;
        color: #fff; }
  .game__bottom {
    width: 100%;
    display: flex;
    margin-top: 10px; }
  .game__chip {
    margin-right: 20px; }
    .game__chip-title {
      font-size: 14px;
      color: #999;
      line-height: 1;
      margin-bottom: 6px; }
    .game__chip-select {
      background-color: #071224;
      border-radius: 6px;
      width: 60px;
      height: 50px;
      background-image: url("../../assets/eos-logo.png");
      background-repeat: no-repeat;
      background-position: 20px center;
      background-size: 17px 25px; }
    .game__chip-desc {
      font-size: 14px;
      color: #999;
      line-height: 1;
      margin-bottom: 6px;
      margin-top: 10px; }
  .game__bet-amount {
    flex: 1;
    margin-right: 20px; }
    .game__bet-amount-title {
      font-size: 14px;
      color: #999;
      line-height: 1;
      margin-bottom: 6px; }
    .game__bet-amount-content {
      background-color: #071224;
      border-radius: 6px;
      width: 100%;
      height: 50px;
      display: flex; }
      .game__bet-amount-content-input {
        border: none;
        margin: 2px;
        width: 110px;
        height: calc(100% - 4px);
        background-color: #1e2b40;
        border-radius: 4px;
        line-height: normal;
        font-size: 20px;
        color: #fff;
        text-align: center; }
      .game__bet-amount-content-btns {
        flex: 1;
        display: flex;
        align-items: stretch;
        font-size: 14px; }
        .game__bet-amount-content-btns > div {
          flex: 1;
          color: #7b7b7b;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer; }
          .game__bet-amount-content-btns > div:hover {
            color: #fff; }
    .game__bet-amount-desc {
      font-size: 14px;
      color: #999;
      line-height: 1;
      margin-bottom: 6px;
      margin-top: 10px; }
  .game__confirm {
    width: 200px;
    padding-top: 20px; }
    .game__confirm > button {
      width: 100%; }

/* 手机上style */
@media only screen and (max-width: 767px) {
  .game {
    width: calc(100% - 20px);
    margin: 20px auto; }
    .game__prediction {
      width: 125px;
      height: 120px;
      color: #fff; }
      .game__prediction-value {
        font-size: 55px; }
      .game__prediction-label {
        font-size: 12px; }
    .game__result {
      width: 125px;
      height: 120px;
      color: #fff; }
      .game__result-value {
        font-size: 55px; }
      .game__result-label {
        font-size: 12px; }
    .game__sm__roll-type {
      margin: 20px 0;
      display: flex;
      justify-content: center; }
      .game__sm__roll-type > button {
        width: 125px;
        margin: 0 10px; }
    .game__info {
      height: 66px; }
      .game__info-item:after {
        top: 15px;
        bottom: 15px; }
      .game__info-item > div:first-child {
        margin-bottom: 10px;
        font-size: 12px; }
      .game__info-item > div:last-child {
        font-size: 14px; }
    .game__bottom {
      flex-wrap: wrap; }
    .game__chip-title {
      font-size: 12px; }
    .game__chip-select {
      width: 60px;
      height: 40px; }
    .game__chip-desc {
      font-size: 12px;
      margin-top: 10px; }
    .game__bet-amount {
      flex: 1;
      margin-right: 0; }
      .game__bet-amount-title {
        font-size: 12px; }
      .game__bet-amount-content {
        height: 40px; }
        .game__bet-amount-content-input {
          font-size: 14px;
          flex: 0 0 33%;
          min-width: 50px; }
        .game__bet-amount-content-btns {
          flex: 1; }
      .game__bet-amount-desc {
        font-size: 12px; }
    .game__confirm {
      padding-top: 10px;
      width: 100%; } }
