@import '../../../styles/variables.scss';

.game-slider {
  &__label-wrap {
    display: flex;
    justify-content: space-between;
    color: #fff;
    font-size: 12px;
  }

  &__less {
    & .rc-slider-rail {
      background-color: transparent;
      border: 1px solid #324970;
    }
    & .rc-slider-track {
      background-color: $win-color;
    }
  }

  &__greater {
    & .rc-slider-rail {
      background-color: $win-color;
    }
    & .rc-slider-track {
      background-color: $center-frame-color;
      border: 1px solid #324970;
    }
  }

  & .rc-slider-rail {
    height: 10px;
  }
  & .rc-slider-track {
    height: 10px;
  }

  & .rc-slider-handle {
    margin-left: -10px;
    margin-top: -5px;
    width: 20px;
    height: 20px;
    border-radius: 5px;
    border: none;
    &:focus {
      box-shadow: none;
    }
  }
}
