$height: 70px;

@mixin menu {
  height: 100%;
  line-height: $height;
  color: #fff;
  text-align: center;
  margin: 0 10px;
  cursor: pointer;

  &:hover {
    color: #ffffffcc;
  }

  &:active {
    color: #ffffff88;
  }
}

.app-navbar {
  width: 100%;
  height: $height;
  background-color: #000;
  color: #fff;

  &__content {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    max-width: 1366px;
    height: 100%;
  }

  &__logo {
    height: 100%;
    width: 200px;
    // background: red;
  }

  &__right {
    display: flex;
  }

  &__login {
    @include menu();
  }
  &__language {
    @include menu();
    // height: 40px;
    // line-height: 1;
    // display: flex;
    // justify-content: center;
    // align-items: center;
  }
}
