@import '../../styles/variables.scss';

.app-button {
  padding: 0 10px;
  box-sizing: border-box;
  border: 1px solid #576477;
  color: #fff;
  border-radius: 6px;
  background-color: $button-color;
  line-height: 1;
  text-align: center;
  cursor: pointer;

  &:hover {
    border-color: $button-hover-color;
    background-color: $button-hover-color;
  }

  &__active {
    border-color: $button-active-color;
    background-color: $button-active-color;

    &:hover {
      border-color: $button-active-hover-color;
      background-color: $button-active-hover-color;
    }
  }

  &__size-small {
    height: 30px;
    font-size: 12px;
  }
  &__size-default {
    height: 42px;
    font-size: 14px;
  }
  &__size-large {
    height: 50px;
    font-size: 18px;
  }
}
